import React from 'react'
import './contact.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { Container } from 'react-bootstrap'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import './glitch.css';

const contact = ({ flexDirection }) =>{
    return(
        <div className="section" style={{ flexDirection: flexDirection }}>
        <Container fluid className="contact">
            <Row>
                <Col>
                    <div className="contactTitle">
                        <h1 class="" data-text="Entre em contato">Entre em contato</h1>
                    </div>
                </Col>
            </Row>
            <Row>
                <Col>
                    <div className="contactSub">
                        <h4>Get in Touch</h4>
                    </div>
                </Col>
            </Row>

            <Row className="align-content-center">
            <div class="col-lg-4 col-centered">
                <Col>
                <Form >
                    <Form.Group controlId="formBasicEmail">
                        <Form.Control type="email" placeholder="Your email" />
                    </Form.Group> 

                <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Control as="textarea" placeholder="Your message" rows={3} />
                </Form.Group> 

                <div className="d-grid gap-2">
                <Button variant="light" size="md" className="align-content-center" type="submit">
                    Enviar
                </Button>
                </div>
                </Form>
                </Col>
                </div>
            </Row>
        </Container>
        </div>
    )
}

export default contact;