import React from 'react'
import './social.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { Container } from 'react-bootstrap'
import './glitch.css';
import bgSocial from "../imgs/bgSocial.png";

const social = ({ flexDirection }) =>{
    return(
        <div className="section" style={{ flexDirection: flexDirection }}>
        <Container fluid className="social" style={{ backgroundImage: 'url(' + bgSocial + ')' }}>


            <Row>
                <Col><div className="espaco"></div></Col>
            </Row>

            <Row fluid>
                <Col>
                    <div className=" corRetangulo retangulo">
                    </div>
                </Col>
                <Col></Col>
            </Row>

            <Row fluid>
                <Col>
                    <div className="socialLinks">
                        <a href="https://www.behance.net/joserafaeldesign" target="_blank" >BEHANCE</a><br />
                        <a href="https://www.instagram.com/joserafaeldesign/" target="_blank">INSTAGRAm</a>
                    </div>
                </Col>
                
            </Row>

            <Row fluid>
            <Col></Col>
            <Col></Col>
                <Col>
                    <div className=" corRetangulo retangulo2 justify-content-end">
                    </div>
                </Col>
            </Row>

        </Container>
        </div>
    )
}

export default social;