import React from 'react'
import './footer.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { Container } from 'react-bootstrap'
import { BsFacebook } from 'react-icons/bs';
import { AiFillInstagram, AiFillBehanceCircle } from 'react-icons/ai'
import './glitch.css';

const footer = ({ flexDirection }) =>{
    return(
        <div className="section" style={{ flexDirection: flexDirection }}>
    <Container fluid fixed="bottom" className="footerContainer">
        <Row>
        <Col>
            <div className="footerText">
          <h1 title="LET'S WORK TOGETHER">LET'S WORK TOGETHER</h1>
            </div>  
        </Col>
        </Row> 

        <Row>
        <Col>
            <div className="socialIcons">
                {/*<BsFacebook className="facebookIcon animation" />{" "}{" "}*/}
                <AiFillInstagram className="animation" />{" "}{" "}
                <AiFillBehanceCircle className="animation"/>
            </div>
        </Col>
        </Row> 

         <Row>  
         <Col> </Col>
             <Col> 
             
                <div className="footer">
                    <b>&copy;2021.Designed by <span>JOSE RAFAEL</span></b>
                </div>
            </Col>
            <Col> </Col>
        </Row>   
    </Container>
    </div>
    )
}
export default footer;