import React from 'react'
import './work.css'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { Container } from 'react-bootstrap'
import Image666 from 'react-bootstrap/Image';

import workGlitch1 from '../imgs/workGlitch1.png';
import workGlitch2 from '../imgs/workGlitch2.png';
import workGlitch3 from '../imgs/workGlitch3.png';

import { ImageGroup, Image } from 'react-fullscreen-image'

const images = [
    workGlitch1,
    workGlitch2,
    workGlitch3,
  ]

const work = ({ flexDirection }) =>{
    return(
        <div fluid className="section" style={{ flexDirection: flexDirection }}>
        <Container fluid className="work">
            <Row>
                <Col>
                    <div className="workTitle">
                        <h1>Work</h1>
                    </div>
                </Col>
                <Col>
                    <div className="japaneseFont2">
                        <h1>仕事</h1>
                    </div>
                </Col>
            </Row>

        <Container fluid>
            <ImageGroup>
        <ul className="images">
          {images.map(i => (
            <li key={i}>
              <Image src={i} alt="work" />
            </li>
          ))}
        </ul>
      </ImageGroup>

      </Container>
        </Container>
        </div>
    )
}

export default work;